<template>
  <div>NotFound</div>
</template>

<script>
import Swal from "sweetalert2";

export default {
  created() {
    Swal.fire({
      title: "  ",
      text: " ",
      icon: "warning",
      buttons: true,
      confirmButtonText: "",
      allowOutsideClick: false,
    }).then((result) => {
      if (result.isConfirmed) {
        this.$router.push({
          name: "home",
        });
      }
    });
  },
};
</script>

