<template>
  <div>
    <router-view />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Swal from "sweetalert2";

export default {
  name: "QnaView",
  computed: {
    ...mapGetters({ isLoggedIn: "isLoggedIn" }),
  },
  created() {
    if (!this.isLoggedIn) {
      Swal.fire({
        title: "  .",
        text: "  ?",
        icon: "warning",
        showCancelButton: true,
        buttons: true,
        dangerMode: true,
        confirmButtonText: "",
        cancelButtonText: "",
      }).then((result) => {
        if (result.isConfirmed) {
          this.$router.push({
            name: "MemberLogin",
          });
        } else {
          this.$router.push({
            name: "home",
          });
        }
      });
    }
  },
};
</script>

