                      variant="solo"
                      bg-color="#F0EDED"
                      required
                      :rules="nicknameRules"
                    ></v-text-field>
                  </div>
                  <div style="flex: 3">
                    <v-btn
                      @click="nickCheck(credentials.nickname)"
                      :disabled="nickDisable"
                      :prepend-icon="nickChecked ? 'mdi-check-circle' : 'mdi-close-circle'"
                      :color="nickChecked ? '#c9deff' : 'red-lighten-4'"
                      style="margin-top: 10px; left: 0"
