              </div>
              <div style="display: flex">
                <div style="flex: 1" />
                <div style="flex: 9; display: flex">
                  <v-text-field
                    label="e-mail"
                    v-model="credentials.email"
                    variant="solo"
                    bg-color="#F0EDED"
                    required
                    :rules="emailRules"
                    @click:append="passwordConfirmShow = !passwordConfirmShow"
                  ></v-text-field>
                </div>
              </div>
            </div>
            <div style="flex: 1">
              <div style="display: flex">
                <div style="flex: 1" />
                <div id="inputPassword" style="flex: 8">
                  <v-text-field
                    label=""
                    bg-color="#F0EDED"
                    variant="solo"
                    v-model="credentials.password"
                    :rules="passwordRules"
                    required
                    :type="passwordShow ? 'text' : 'password'"
                    :append-icon="passwordShow ? 'mdi-eye' : 'mdi-eye-off'"
                    @click:append="passwordShow = !passwordShow"
                    style="font-family: Georgia; font-weight: bold"
                  ></v-text-field>
                </div>
                <div style="flex: 1" />
              </div>
              <div style="display: flex">
                <div style="flex: 1" />
                <div id="inputPassword" style="flex: 8">
                  <v-text-field
                    label=" "
                    bg-color="#F0EDED"
                    variant="solo"
                    v-model="credentials.passwordConfirm"
                    :rules="passwordConfirmRules"
                    required
                    :type="passwordConfirmShow ? 'text' : 'password'"
                    :append-icon="passwordConfirmShow ? 'mdi-eye' : 'mdi-eye-off'"
                    @click:append="passwordConfirmShow = !passwordConfirmShow"
                    style="font-family: Georgia; font-weight: bold"
                  ></v-text-field>
                </div>
                <div style="flex: 1" />
              </div>

              <div style="display: flex">
                <div style="flex: 1" />
                <div style="flex: 8">
                  <v-select
                    v-model="credentials.sidoCode"
                    :items="sido"
                    item-title="name"
                    item-value="code"
                    label=" "
                    persistent-hint
                    single-line
                    bg-color="#F0EDED"
                  ></v-select>
                </div>
                <div style="flex: 1" />
              </div>
            </div>
          </div>

          <v-card
