                 8  ( 3    )<br />
                       3  ,
                3           
                 .<br />
                    3    
                     ,     .<br />
                ,          
                 .<br />
                1.    ,       <br />
                2.         
                    <br />
                <br />
                 6  (   )<br />
                           .<br />
