                <br />
                 7  ()<br />
                         15  26   
                        .       
                     .<br />
                <br />
                 8  ( )<br />
                           
                      .<br />
                           
                          .<br />
                <br />
                 9  (    )<br />
                 ,     .<br />
                : TRAVEL-ON<br />
                <br />
                :    3  <br />
                <br />
                : 010-2812-0890<br />
                <br />
                           
                      .<br />
                  :  BE (  )<br />
                 : t21212@naver.com<br />
                <br />
              </div>
            </v-card-text>
          </v-card>
          <v-checkbox v-model="agreeCheck" color="deep-purple">
            <template v-slot:label>
               &nbsp;
              <a href="#" style="text-decoration: underline">   </a>
              &nbsp;  .()
            </template>
          </v-checkbox>
        </v-form>
        <div class="mb-5" style="display: flex">
          <div style="flex: 1" />
          <div style="flex: 8">
            <v-btn
              block
              :disabled="!valid || !agreeCheck || !idChecked || idDisable || !nickChecked || nickDisable"
              @click="regist(credentials)"
              size="large"
              style="margin-bottom: 30px; background-color: #c9deff"
              ></v-btn
            >
          </div>
          <div style="flex: 1" />
        </div>
      </div>
    </div>
    <div style="flex: 1" />
  </div>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";
import spring from "@/api/spring_boot";
import Swal from "sweetalert2";

export default {
  data() {
    return {
      credentials: {
        id: "",
        password: "",
        passwordConfirm: "",
        email: "",
        nickname: "",
        sidoCode: "",
      },
      expand: false,
      agreeCheck: false,
      sido: [
        { code: "4200000000", name: "" },
        { code: "4100000000", name: "" },
        { code: "4800000000", name: "" },
        { code: "4700000000", name: "" },
        { code: "2900000000", name: "" },
        { code: "2700000000", name: "" },
        { code: "3000000000", name: "" },
        { code: "2600000000", name: "" },
        { code: "1100000000", name: "" },
        { code: "3611000000", name: "" },
        { code: "3100000000", name: "" },
        { code: "2800000000", name: "" },
        { code: "4600000000", name: "" },
        { code: "4500000000", name: "" },
        { code: "5000000000", name: "" },
        { code: "4400000000", name: "" },
        { code: "4300000000", name: "" },
      ],
      valid: false,
      passwordShow: false,
      passwordConfirmShow: false,
      idDisable: true,
      nickDisable: true,
      idChecked: "",
      nickChecked: "",
      nameRules: [
        (v) => !!v || " .",
        (v) => /^[a-zA-Z]+[0-9]+[a-z0-9A-Z]*$/.test(v) || "  +  .",
        (v) => (v.length >= 6 && v.length <= 16) || "  6 16 .",
      ],
      passwordRules: [
        (v) => !!v || " .",
        (v) =>
          /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,15}$/.test(v) ||
          " 8 ~ 16 , ,     .",
      ],
      passwordConfirmRules: [
        (v) => !!v || "  .",
        (v) => v === this.credentials.password || "  .",
      ],
      nicknameRules: [
        (v) => !!v || " .",
        (v) => v.length <= 10 || " 10  .",
      ],
      emailRules: [
        (v) => !!v || "E-mail .",
        (v) =>
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
            v,
          ) || "E-mail .",
      ],
    };
  },
  watch: {
    /* eslint-disable */
    "credentials.id": function () {
      this.idChecked = "";
      this.idDisable = false;
      this.nameRules.forEach((element) => {
        if (typeof element(this.credentials.id) !== "boolean") {
          this.idDisable = true;
        }
      });
    },
    // eslint-disable-next-line func-names
    "credentials.nickname": function () {
      this.nicknameChecked = "";
      this.nickDisable = false;
      this.nicknameRules.forEach((element) => {
        if (typeof element(this.credentials.nickname) !== "boolean") {
          this.nickDisable = true;
        }
      });
    },
  },
  computed: {},
  mounted() {},
  methods: {
    ...mapActions(["regist"]),
    idCheck(id) {
      /*
      id
        
          idChecked  True
        
          -
        idChecked True  
      */
      if (id !== "") {
        axios({
          url: spring.accounts.idCheck(),
          method: "post",
          params: { id },
        })
          .then(() => {
            this.idChecked = id;
            Swal.fire({
              icon: "success",
              title: "   !",
              showConfirmButton: false,
              timer: 1000,
            });
          })
          .catch((err) => {
            Swal.fire({
              icon: "error",
              title: "   !",
              showConfirmButton: false,
              timer: 1000,
            });
            console.log(err);
          });
      } else {
        Swal.fire({
          icon: "error",
          title: "  .",
          showConfirmButton: false,
          timer: 1000,
        });
      }
    },
    nickCheck(nickname) {
      if (nickname !== "") {
        console.log(nickname);
        axios({
          url: spring.accounts.nickCheck(),
          method: "post",
          params: { nickname },
        })
          .then((res) => {
            console.log(res);
            this.nickChecked = nickname;
            Swal.fire({
              icon: "success",
              title: "   !",
              showConfirmButton: false,
              timer: 1000,
            });
          })
          .catch((err) => {
            Swal.fire({
              icon: "error",
              title: "   !",
              showConfirmButton: false,
              timer: 1000,
            });
            console.log(err);
          });
      } else {
        Swal.fire({
          icon: "error",
          title: "  .",
          showConfirmButton: false,
          timer: 1000,
        });
      }
    },
  },
};
</script>

<style scoped>
.bg {
  width: 100vw;
  height: 100%;
}
</style>
